
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


function camelCase (input) {
let str = input.toLowerCase().replace(/-(.)/g, function (match, group1) {
return group1.toUpperCase()
})

str = str.replace(/_(.)/g, function (match, group1) {
return group1.toUpperCase()
})
return str.slice(0, 1).toUpperCase() + str.slice(1)
}

export default {
name: 'ClassList',
components: {
Flexbox,
FlexboxItem
},
data () {
return {
height: window.innerHeight - 46 - 53,
components: this.split([{
name: '计算器',
icon: '&#xe624;',
url: 'calculator/one'
},
{
name: '11计算器',
icon: '&#xe624;',
url: 'calculator/two'
}])
}
},
filters: {
camelCase
},
activated () {
document.querySelector('#demo_list_box').scrollTop = this.demoTop
},
methods: {
go (name) {
if (name === '' || name === undefined || name === null) {
} else {
this.$router.push({path: `/mobile/${name}`})
}
},
split (array) {
let chunks = []
let count = Math.ceil(array.length / 3)
while (count > 0) {
chunks.push(array.slice((count - 1) * 3, count * 3))
count--
}
chunks = chunks.reverse()
const lastList = chunks[chunks.length - 1]
const lastLength = lastList.length
if (lastLength < 3) {
for (let i = 0; i < 3 - lastLength; i++) {
lastList.push({
name: '----',
icon: ''
})
}
}
return chunks
}
},
computed: {
}
}



