
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapActions } from 'vuex'
export default {
directives: {
TransferDom
},
components: {
Radio,
Group,
Cell,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet
},
data () {
return {
showMenu: false,
menus: {
'language.noop': '<span class="menu-title">Language</span>',
'zh-CN': '中文',
'en': 'English'
},
drawerVisibility: false,
showMode: 'push',
showModeValue: 'overlay',
showPlacement: 'left',
showPlacementValue: 'left'
}
},

created: function () {
this.changeLanguage()
},
computed: {
...mapState({
route: state => state.route,
path: state => state.route.path,
deviceready: state => state.app.deviceready,
demoTop: state => state.vux.demoScrollTop,
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction
}),

isTabbarDemo () {

return /calculator/.test(this.$route.path)
},

title () {
return this.$route.name ? `${this.$route.name}` : 'title'
},
leftOptions () {
return {
showBack: this.$route.path !== '/mobile'
}
},
rightOptions () {
return {
showMore: true
}
}
},
methods: {
...mapActions([
'updateDemoPosition'
]),

onClickMore () {
this.showMenu = true
},

changeLocale (locale) {
let lang
if (locale === 'zh-CN') {
lang = 'zh-CN'
this.$i18n.locale = lang 
this.$store.dispatch('ToggleLanguage', lang)
} else {
lang = 'en-US'
this.$i18n.locale = lang 
this.$store.dispatch('ToggleLanguage', lang)
}
},

changeLanguage () {
this.$i18n.locale = this.$store.state.cookie.language
}
}
}



